import React, {Component} from 'react'
import Helmet from 'react-helmet'
import AppIcons from '../partials/AppIcons'
import FooterInfo from '../partials/FooterInfo'

const displayBlock = {
  display: 'block'
}

const displayNone = {
  display: 'none'
}

class WorkplacePage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      modalToggle: false
    }

    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(e) {
    if(e.target.className === 'workplace-modalbg' || e.target.className === 'modal-close') {
      this.setState({
        modalToggle: false
      });
    } else {
      this.setState({
        modalToggle: true
      });
    }
  }

  render() {
    return (
      <div>
        <Helmet
          title="Fitato for Workplace"
          meta={[
            { name: 'description', content: 'The fitness fix for the Indian marketplace.' },
          ]}
        />
        <div className="workplace-page">
          
          <div className="workplace-section-intro" style={{backgroundImage: 'url(static/workplace/yoga-1.jpeg)'}}>
            <div className="container">
              <div className="workplace-section-intro__content">
                <h1>Activate <span className="normal">a Healthy Workplace</span>.</h1>
                <p>Happier, healthier more productive employees? Who wouldn’t want that? Connect your employees to over 300 studios and gyms around the city, all through your single Fitato pass. Activate the Fitato life at your workplace.</p>
                <span className="button button-fluid" onClick={this.handleClick}>Book a call</span>
              </div>
            </div>
          </div>
    
          <div className="workplace-section-onepass">
            <div className="container">
              <h2>One pass. 300+ fitness centres. For each employee.</h2>
            </div>
          </div>
          
          <div className="workplace-section-about" style={{backgroundImage: 'url(static/workplace/athletes-endurance-energy.jpg)'}}>
            <div className="container">
              <h2>What is Fitato for Workplace?</h2>
              <p>Fitato is the largest fitness network that offers universal membership to 300+ best gyms and fitness centers around the Pune city and on-demand fitness classes at your workplace. One pass for every employee fitness need. Workout anytime, anywhere, in a facility next to your office or home.</p>
              <p>Shape a culture of fitness in your team with access to premium fitness classes and the tools to increase motivation and connection.</p>
              <span className="button button-fluid" onClick={this.handleClick}>Get in touch</span>
            </div>
          </div>
    
          <div className="workplace-section-employees">
            <div className="container">
            <h2>For Employees</h2>
            <p>The biggest workout blockers? Convenience, lack of motivation and most importantly lack of flexibility. Fitato’s flexible fitness membership makes working out actually work out for your team.</p>
            <p>The network comprises the best gyms and fitness studios in the city. Whether your team wants to go to the gym, yoga class or de-stress with a swimming session or pump up with a boxing class, you can go to the same facility every day or try a new one.</p>
            <p>Fitato is the perfect solution and fitness is right around the corner. All with a simple aim enabling your team that hour a day where they connect with their mind, body, and soul.</p>
            <h2>For Employers</h2>
            <p>The benefits of working out extend far beyond the gym: studies show it improves concentration, enhances creativity and lowers stress. Partner with Fitato and give your employees access to the classes they need to stay motivated personally and professionally.</p>
            <p>Better yet? Your employees can work out together to build their strength and their sense of team. Plus, all corporate clients receive an onsite kickoff, custom landing pages, communication and templates, monthly reports and dedicated account management support to ensure your employees stay engaged.</p>
            </div>
          </div>
    
          <div className="workplace-section-why" style={{backgroundImage: 'url(static/workplace/gym-1.jpeg)'}}>
            <div className="container">
              <h2>The Right Health Solution for an Indian Workplace</h2>
              <div className="workplace-section-why__items">
                <div className="workplace-section-why__item">
                  <div className="workplace-section-why__item--left">
                    <img src="static/icons/card.svg" />
                  </div>
                  <div className="workplace-section-why__item--right">
                    <p>Cost-effecient plans, perfect for organizations.</p>
                  </div>
                </div>
                <div className="workplace-section-why__item">
                  <div className="workplace-section-why__item--left">
                    <img src="static/icons/barbell.svg" />
                  </div>
                  <div className="workplace-section-why__item--right">
                    <p>On-demand fitness for everyone in your organisation.</p>
                  </div>
                </div>
                <div className="workplace-section-why__item">
                  <div className="workplace-section-why__item--left">
                    <img src="static/icons/face.svg" />
                  </div>
                  <div className="workplace-section-why__item--right">
                    <p>Variety of activities makes fitness fun.</p>
                  </div>
                </div>
                <div className="workplace-section-why__item">
                  <div className="workplace-section-why__item--left">
                    <img src="static/icons/clock.svg" />
                  </div>
                  <div className="workplace-section-why__item--right">
                    <p>Offers flexibility of time and place.</p>
                  </div>
                </div>
              </div>
              <span className="button align-center" onClick={this.handleClick}>Arrange a Call Back</span>
            </div>
          </div>
    
          <div className="workplace-section-classes" style={{backgroundImage: 'url(static/workplace/fitness-bg.jpeg)'}}>
            <div className="container">
              <h2>Variety of On-Demand Classes</h2>
              <div className="workplace-section-classes__items">
                <div className="workplace-section-classes__item">
                  <p>Yoga</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>Self-Defence</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>MMA</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>Zumba</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>Meditation</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>Aqua Zumba Event</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>Capoeira</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>Drumcircles</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>Kho Kho</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>HIIT</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>Functional Training</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>Bokwa</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>Piloxing</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>Biet Workshop</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>Dance</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>Aerobics</p>
                </div>
              </div>
            </div>
          </div>
    
          <div className="workplace-section-happy" style={{backgroundImage: 'url(static/workplace/achievement.jpg)'}}>
            <div className="container">
              <h2>Testimonials</h2>
              <p><em>"Our team has loved such an innovative and personalized approach to wellness. It gives us all the opportunity to go to new styles of fitness we've been wanting to try for a while. Best of all, many of our employees use Fitato to stay active together."</em></p>
            </div>
          </div>
    
          <div className="workplace-section-plans">
            <div className="container">
              <h2>Our Membership Plans</h2>
              <div className="workplace-section-plans__items">
                <div className="workplace-section-plans__item">
                  <div className="workplace-section-plans__item--title">
                    <h3>Individual Employee Plan</h3>
                  </div>
                  <div className="workplace-section-plans__item--content">
                    <p>Offer for individual employees<br />Complete access to Fitato brank network<br />Membership purchased by individual</p>
                    <p><strong>Access 250+ Gyms &amp; Fitness Studios</strong></p>
                    <span className="button button-fluid" onClick={this.handleClick}>Get Started</span>
                  </div>
                </div>
                <div className="workplace-section-plans__item">
                  <div className="workplace-section-plans__item--title">
                    <h3>Corporate All Access Pass</h3>
                  </div>
                  <div className="workplace-section-plans__item--content">
                    <p>Package to be purchased by the company<br />Premium Fitato access and a complete wellness plan<br />Monthly fitness/wellness sessions in office campure</p>
                    <p><strong>Additional Diet &amp; Wellness Sessions</strong></p>
                    <span className="button button-fluid" onClick={this.handleClick}>Get Started</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
    
          <div className="workplace-section-app">
            <div className="container">
              <div className="workplace-section-app__item">
                <h2>Give employees health and fitness at fingertips.</h2>
                <p>Become a Fitato member, choose your next workout from many categories and reserve the activity — all through the Fitato app.</p>
                <AppIcons />
              </div>
              <div className="workplace-section-app__item align-center">
                <img src="static/index/section-four-2.png" alt="fitato smartphone app" />
              </div>
            </div>
          </div>
    
          <div className="workplace-section-cta" style={{backgroundImage: 'url(static/workplace/gymnist.jpeg)'}}>
            <div className="container">
              <h2>Fitato can drive better outcomes for your organization.</h2>
              <span className="button" onClick={this.handleClick}>Let us help</span>
            </div>
          </div>
    
        </div>

        <div className="workplace-modalbg" 
          style={(this.state.modalToggle) ? displayBlock : displayNone} onClick={this.handleClick}>
          <div className="workplace-modal">
            <div className="workplace-modal__title">
              <h3>Learn More About the Fitato Corporate Program</h3>
            </div>
            <span className="modal-close" onClick={this.handleClick}>X</span>
            <div className="workplace-modal__form">
              <small>Please fill out this form, and we&apos;ll get in touch shortly.</small>
              <form className="contact-form">
                <div><input type="text" name="name" placeholder="Personal Name" required /></div>
                <div><input type="text" name="cname" placeholder="Company Name" required /></div>
                <div><input type="tel" name="phone" placeholder="Phone Number" required /></div>
                <div><input type="email" name="email" placeholder="Business Email" required /></div>
                <div><input type="number" name="team-size" placeholder="Team Size" required /></div>
                <input type="submit" value="Submit" />
              </form>
            </div>
          </div>
        </div>

        <FooterInfo />
      </div>
    )
  }
}

export default WorkplacePage



